
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

.postCommentContainer {
  border: 2px solid #1E88E5;
  background: #42A5F5;
  padding: 6px;
  border-radius: 3px;
}
textarea, .postCommentContainer, .postCommentToolbar {
  width: 100%;
  background: white;
  padding: 0;
}
textarea{
  border: 0;
  border-radius: 3px;
  background: none;
}
.postCommentToolbar {
  text-align: right;
  background: #1E88E5;
}

h1 {
  text-align: left;
  font-size: 16px;
  font-weight: bold;
}

h3 {
  text-align: left;
}

.commentBox {
  margin-top: 16px;
  margin-bottom: 16px;
  text-align: left;
  clear: both;
}
.date{
  color: #555555;
  font-weight: normal;
  font-size: 75%;
  -webkit-text-decoration: italic;
          text-decoration: italic;
  text-align: left;
}
.comment {
  padding-left: 64px;
  text-align: left;
}
.name {
  padding-left: 64px;
  color: #2196F3;
  font-weight: bold;
  padding-bottom: 4px;
  text-align: left;
}
.avatar img {
  width: 48px;
  height: 48px;
  float: left;
  border-radius: 4px;
}
body{
  background: black;
}
